import React from "react";

//Styles
import "../css/covid.css";

//Pics
import cov from "../img/cov.png";

//Components
import CovidStats from "../components/CovidPage/CovidStats";
import CovidAdvice from "../components/CovidPage/CovidAdvice";

class Covid19 extends React.Component {
    render() {
        return(
            <div>
                <article className="markdown-body">
                    <section className="profile">
                        <img className="avatar" src={cov}/>
                            <h1 className="title">
                                COVID-19
                            </h1>
                            <p className="description">Самая важная и необходимая {`\n`}
                                информация
                            </p>
                    </section>
                    <section className="covid-wrapper">
                        <CovidStats />
                    </section>
                    <section>
                        <h2>
                            <label className="emoji exclam"></label>
                            Важное
                        </h2>
                        <CovidAdvice />
                    </section>
                </article>
            </div>
        );
    }
}

export default Covid19