import React from 'react';

//Pic's for rand ava
import photo_one from '../../photos/1.png';
import photo_two from '../../photos/2.png';
import photo_three from '../../photos/3.png';

class Profile extends React.Component {
    render() {
        return(
            <div>
                <img className="avatar" id="ava" alt="Это Я."/>
                <h1 className="title">{this.props.name}</h1>
                <p className="description">{this.props.description}</p>
            </div>
        );
    }

    componentDidMount() {
        const avatar = document.getElementById('ava');
        let randNum = Math.floor(Math.random() * 3);

        if (avatar) {
            switch (randNum) {
                case 0:
                    avatar.src = photo_one;
                    console.log('Рандом аватарка: ', randNum);
                break;

                case 1:
                    avatar.src = photo_two;
                    console.log('Рандом аватарка: ', randNum);
                break;

                case 2:
                    avatar.src = photo_three;
                    console.log('Рандом аватарка: ', randNum);
                break;
            }
        }

        else {
            console.log('Аватарку сменить нельзя! Что-то пошло не так :(');
        }
    }
}

export default Profile