import React from "react";

//Components
import CovidAlert from "../components/CovidPage/CovidAlert";
import Profile from "../components/MainPage/Profile";
import Links from "../components/MainPage/Links";
import History from "../components/MainPage/History";

class MainPage extends React.Component {
    render() {
        return(
            <div>
                <article className="markdown-body">
                    <section className="covid-section">
                        <CovidAlert
                            title = "COVID-19 Alert"
                            boldTitle = "Без паники!"
                        />
                    </section>

                    <section className="profile">
                        <Profile
                            name = "Макс Чёрный"
                            description = {`Добро пожаловать в моё цифровое
                                представительство!
                            `}
                        />
                    </section>
                    
                    <section>
                        <h2>
                            <label className="emoji coder"></label>
                            Чем занимаюсь?
                        </h2>

                        <p>
                            Пишу код, делаю дизайн,
                            {`\n`}<a href="https://soundcloud.com/verlorenmusic" target="_blank" rel="noopener">творю музыку</a>{`\n`}
                            и много ораторствую.
                        </p>
                    </section>

                    <section>
                        <h2>
                            <label className="emoji map"></label>
                            Где тусуюсь?
                        </h2>

                        <Links/>
                    </section>

                    <section>
                        <h2>
                            <label className="emoji tools"></label>
                            Орудия труда
                        </h2>

                        <p>
                            Мозг, десницы и iPhone 11, JS, NodeJS, C#, jPHP, Windows 10, Kali/Ubuntu, WebStorm, Adobe Photoshop, Adobe
                            Illustrator, Adobe Xd, Adobe InDesign, Figma, Affinity Designer, Adobe Audition, Ableton.
                        </p>
                    </section>

                    <section>
                        <h2>
                            <label className="emoji history"></label>
                            Летопись
                        </h2>

                        <ul>
                            <li>
                                <History
                                    year = "2019"
                                    title = "Дизайн: айдентика для футболок девятого класса"
                                    url = "https://vk.com/@9beforever-merch"
                                    emoji = "#Дизайн"
                                />
                            </li>

                            <li>
                                <History
                                    year = "2018"
                                    title = "Бот уничтожитель-захватчик бесед ВКонтакте (Заморожено)"
                                    url = "https://vk.com/cliffford"
                                    emoji = "#Разработка"
                                />

                                <History
                                    title = "Музыка: создание Verloren Music"
                                    url = "https://vk.com/verlorenmusic"
                                    emoji = "#Музыка"
                                />
                            </li>

                            <li>
                                <History
                                    year = "2014"
                                    title = "Начало: школьник-блоггер"
                                    url = "https://youtube.com/TrollZor"
                                />
                            </li>
                        </ul>
                    </section>
                </article>
            </div>
        );
    }
}

export default MainPage