import React from 'react';

import s_face from "../../emoji/sneezing_face.png";
import w_face from "../../emoji/winking_f.png";
import skull from "../../emoji/skull.png";

class CovidStats extends React.Component {
    render() {
        return(
            <div>
                <div className="covid-monitoring">
                    <div className="covid-card">
                        <img className="covid-img" src={s_face}/>
                        <h3>Заражено</h3>
                        <p id="covid-infected">-</p>
                    </div>

                    <div className="covid-card">
                        <img className="covid-img" src={w_face}/>
                        <h3>Выздоровело</h3>
                        <p id="covid-recovered">-</p>
                    </div>

                    <div className="covid-card">
                        <img className="covid-img" src={skull}/>
                        <h3>Умерло</h3>
                        <p id="covid-deaths">-</p>
                    </div>
                </div>
            </div>
        );
    }

    componentDidMount() {
        async function getData(url = '') {
            const response = await fetch(url);
            return await response.json();
        }

        getData('https://coronavirus-19-api.herokuapp.com/all')
            .then((data) => {
                if (data) {
                    if (document.getElementById('covid-infected') && document.getElementById('covid-recovered') && document.getElementById('covid-deaths')) {
                        document.getElementById('covid-infected').innerText = numForm(data.cases);
                        document.getElementById('covid-recovered').innerText = numForm(data.recovered);
                        document.getElementById('covid-deaths').innerText = numForm(data.deaths);
                    }

                    else {
                        console.log('Elements not found!');
                    }
                }

                else {
                    console.log('Unable to get data!');
                }
        });

        //for format numbers
        function numForm(number, decimals, dec_point, thousands_sep) {
            number = (number + '').replace(/[^0-9+\-Ee.]/g, '');
            var n = !isFinite(+number) ? 0 : +number,
                prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
                sep = (typeof thousands_sep === 'undefined') ? ' ' : thousands_sep,
                dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
                s = '',
                toFixedFix = function(n, prec) {
                    var k = Math.pow(10, prec);
                    return '' + (Math.round(n * k) / k)
                        .toFixed(prec);
                };
            // Fix for IE parseFloat(0.55).toFixed(0) = 0;
            s = (prec ? toFixedFix(n, prec) : '' + Math.round(n))
                .split('.');
        
            if (s[0].length > 3) {
                s[0] = s[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, sep);
            }
        
            if ((s[1] || '')
                .length < prec) {
                s[1] = s[1] || '';
                s[1] += new Array(prec - s[1].length + 1)
                    .join('0');
            }
        
            return s.join(dec);
        }
    }
}

export default CovidStats