import React from 'react';
import Swal from "sweetalert2";

//Icons
import phone from "../../icons/covid19/phone.svg";
import news from "../../icons/covid19/news.svg";
import recom from "../../icons/covid19/recom.svg";
import sites from "../../icons/covid19/sites.svg";

class CovidAdvice extends React.Component {
    render() {
        return(
            <div>
                <div className="color-cards_wrapper">
                    <div className="color-cards">
                        <div className="color-card" id="phone">
                            <img src={phone}/>
                            <h3>Единая горячая линия</h3>
                        </div>
                        <div className="color-card" id="news">
                            <img src={news}/>
                            <h3>Новости о COVID-19</h3>
                        </div>
                        <div className="color-card" id="recom">
                            <img src={recom}/>
                            <h3>Рекомендации и советы</h3>
                        </div>
                        <div className="color-card" id="sites">
                            <img src={sites}/>
                            <h3>Другие ресурсы</h3>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    
    componentDidMount() {
        document.getElementById('phone').addEventListener('click', () => {
            Swal.fire({
                title: 'Единая горячая линия',
                html:
                    'Телефон: <b><a href="tel:88002000112">8‑800‑2000‑112</a></b>',
                showCloseButton: true,
                showConfirmButton: false
            });
        });

        document.getElementById('news').addEventListener('click', () => {
            window.open('https://yandex.ru/news/rubric/koronavirus', '_blank');
        });

        document.getElementById('recom').addEventListener('click', () => {
            Swal.fire({
                title: 'Рекомендации',
                html:
                    'Пожалуйста, прислушайтесь к рекомендациями — это важно!<br><br>' +
                    '<b>• Чихайте и кашляйте в сгиб локтя или салфетку</b><br>' +
                    '<b>• Мойте руки чаще</b><br>' +
                    '<b>• Не прикасайтесь руками к лицу</b><br>' +
                    '<b>• Если есть возможность, избегайте общественные места</b><br>' +
                    '<b>• Ограничьте поездки</b><br>' +
                    '<b>• При обнаружении симптомов срочно обращайтесь за помощью</b>',
                showCloseButton: true,
                showConfirmButton: false
            });
        });

        document.getElementById('sites').addEventListener('click', () => {
            Swal.fire({
                title: 'Другие ресурсы',
                html:
                    '<a href="https://who.int" target="_blank" rel="noopener">Сайт Всемирной организации здравоохранения</a><br>' +
                    '<a href="https://www.rosminzdrav.ru/" target="_blank" rel="noopener">Сайт Минздрав России</a><br>' +
                    '<a href="https://www.arcgis.com/apps/opsdashboard/index.html#/bda7594740fd40299423467b48e9ecf6" target="_blank" rel="noopener">Мониторинг COVID-19</a>',
                showCloseButton: true,
                showConfirmButton: false
            });
        });
    }
}

export default CovidAdvice
