import React from "react";

class History extends React.Component {
    render() {
        return(
            <div>
                <b>{this.props.year}</b>
                <ul>
                    <li>
                        <a target="_blank" rel="noopener" href={this.props.url}>{this.props.title}</a>{` `}
                        <label title={this.props.emoji} className="emoji"></label>
                    </li>
                </ul>
            </div>
        );
    }
}

export default History