/*
    COVID-19 Alert Box
    by Max Black.
*/

import React from 'react';
import { Link } from 'react-router-dom';

import '../../css/covid.css';

class CovidAlert extends React.Component {
    render() {
        return(
            <div>
                <div className="covid-alert">
                    <p className="covid-title">{this.props.title}</p>
                    <p className="covid-description">
                    <b>{this.props.boldTitle}{`\n`}</b>
                        <Link to="/covid19">
                            Победить вирус
                        </Link> можно только совместными усилиями
                    </p>
                </div>
            </div>
        );
    }
}

export default CovidAlert